/* intro */
.bottom-img {
	position: absolute;
	bottom: 20px;
	padding-left: 20px;
	padding-right: 20px;
}

.logo-txt img {
	height: 40px;
}

.bottom-img img {
	height: 256px;
}

.pos-logo {
	padding-top: 40%;
}

.pos-logo.long {
	padding-top: 60%;
}

.img-logo img {
	height: 50px;
}

.img-logo.big img {
	height: 86px;
}

.pos-bottom {
	width: calc(100% - 40px);
	left: 20px;
	position: absolute;
	bottom: 20px;
}

.pos-bottom.middle {
	bottom: 40px;
}

.pos-bottom.long {
	width: calc(100% - 40px);
	left: 20px;
	position: absolute;
	bottom: 90px;
}

.card-container {
	position: absolute;
	width: calc(100% - 40px);
	top: 29px;
	left: 20px;
	height: calc(100% - 120px);
	background: #ffffff;
	box-shadow: 3px 5px 22px 0 rgba(0, 0, 0, 0.1);
	border-radius: 5px;
	padding: 35px 20px 0px 20px;
	box-sizing: border-box;
	position: relative;
}

.card-bottom-txt {
	position: absolute;
	bottom: 20px;
}
