@charset "utf-8";
.main {
  position: relative;
}

.main-content {
  margin-top: 73px;
}

.bothSpace {
  padding-left: 20px;
  padding-right: 20px;
}

.title-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}

.title-container .title-help-icon {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 45px;
  height: 30px;
  border: 1px solid var(--border-gray);
  border-radius: 22px;
}

.title-container .title-help-icon img {
  width: 15px;
  height: 15px;
}

.tab-container {
  display: flex;
  flex-wrap: nowrap;
  overflow-y: auto;
  margin-bottom: 22px;
}

.tab-container.full {
  flex-wrap: wrap;
}

.tab-container.full .tab.active {
  padding-bottom: 0;
  border-bottom: none;
}

.tab-container.full .tab p {
  border-bottom: 0;
  position: relative;
  padding-bottom: 8px;
}

.tab-container.full .tab.active p {
  display: inline-block;
}

.tab-container.full .tab p:after {
  content: "";
  width: 0;
}

.tab-container.full .tab.active p:after {
  content: "";
  position: absolute;
  left: 0;
  bottom: 0px;
  width: 100%;
  background-color: var(--primary);
  height: 2px;
  transform-origin: left;
  transition: 0.3s width cubic-bezier(0.215, 0.61, 0.355, 1);
}

.tab-container .tab {
  font-weight: 400;
  font-size: 13px;
  flex-shrink: 0;
  color: var(--check-gray);
  margin-left: 20px;
  margin-right: 20px;
}

.tab-container .tab p {
  content: "";
  position: relative;
  padding-bottom: 8px;
}

.tab-container .tab p:after {
  content: "";
  position: absolute;
  width: 0;
}

.tab-container .tab.active p:after {
  content: "";
  position: absolute;
  left: 0;
  bottom: 0px;
  width: 100%;
  background-color: var(--primary);
  height: 2px;
  transform-origin: left;
  transition: 0.3s width cubic-bezier(0.215, 0.61, 0.355, 1);
}

.tab-container .tab.active {
  color: black;
}

.img-list-container {
  height: 320px;
  background-repeat: no-repeat;
  background-size: cover;
  box-sizing: border-box;
  border-radius: 5px;
  padding: 0 13px 20px 13px;
  position: relative;
}

.img-list-container.auto {
  height: 100%;
}

.img-list-container.norounded {
  border-radius: 0;
}

.img-list-container.middle {
  height: 320px;
  padding: 0 45px 40px 20px;
}

.img-list-container.long {
  height: 520px;
  margin-bottom: 20px;
}

.img-list-container.long .bottom-container {
  width: calc(100% - 40px);
  left: 20px;
}

.inner-content {
  color: white;
  overflow: hidden;
  text-overflow: ellipsis;
  max-height: 120px;
  min-height: 90px;
  display: -webkit-box;
  -webkit-line-clamp: 4;
  -webkit-box-orient: vertical;
}

.img-list-container.middle .bottom-container {
  bottom: 40px;
}

.img-list-container .bottom-container {
  width: calc(100% - 40px);
  left: 20px;
  position: absolute;
  bottom: 20px;
  box-sizing: border-box;
}

.top-icon-list {
  display: flex;
  align-items: center;
  margin-bottom: 10px;
}

.top-icon {
  display: inline-block;
  background-color: white;
  border-radius: 22px;
  padding: 1px 10px;
  margin-right: 10px;
  color: var(--primary);
  border: 1px solid var(--border-gray);
}

.top-icon.no-space {
  margin-right: 0;
}

.primary-color {
  color: var(--primary);
}

.over-ray-con {
  position: absolute;
  background-color: rgba(0, 0, 0, 0.7);
  width: 100%;
  height: 136px;
  display: flex;
  align-items: center;
  padding-left: 12px;
  padding-right: 12px;
  box-sizing: border-box;
}

.type-delete {
  position: relative;
}

.horiz-item.type-delete .top-desc-con {
  justify-content: flex-start;
}

.top-desc-con {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
  margin-top: 3px;
}

.date-txt {
  font-size: 10px;
  color: var(--check-gray);
}

.list-content {
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  font-size: 14px;
  color: var(--mainBlack);
  white-space: normal;
  line-height: 20px;
  height: 40px;
  overflow: hidden;
  text-overflow: ellipsis;
}

.list-bottom-back {
  display: flex;
  align-items: center;
  justify-content: space-around;
  padding: 15px 10px;
  text-align: center;
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
}

.list-bottom-back.has-tag {
  overflow-y: auto;
  justify-content: flex-start;
  flex-wrap: nowrap;
}

.horiz-item {
  padding-left: 20px;
  padding-right: 20px;
  margin-bottom: 10px;
}

.horiz-inner {
  background: #ffffff;
  box-shadow: 3px 8px 22px 0 rgba(0, 0, 0, 0.05);
  border-radius: 5px;
}

.pos-delete {
  transform: translateX(-80px);
  position: relative;
}

.delete-item {
  top: 0;
  right: 30px;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  border: 1px solid var(--primary);
  border-radius: 50%;
  width: 40px;
  height: 40px;
  background-image: url("../img/trash.png");
  background-repeat: no-repeat;
  background-position: center;
  background-size: 20px;
}

.delete-item img {
  width: 17px;
}

.horiz-ima-box {
  margin-right: 10px;
}

.horiz-ima-box img {
  width: 100px;
  height: 80px;
  border-radius: 5px;
}

.avatar-img-con {
  text-align: center;
}

.avatar-img-con.small img {
  width: 56px;
  height: 56px;
  border-radius: 50%;
}

.avatar-img-con img {
  width: 130px;
  height: 130px;
  border-radius: 50%;
}

.empty-header {
  height: 24px;
}

.only-x-header {
  height: 44px;
  position: relative;
}

.only-x-header .header-x {
  position: absolute;
  right: 20px;
  bottom: 10px;
  width: 24px;
  height: 24px;
}

.text-box {
  background: #ffffff;
  box-shadow: 3px 5px 22px 0 rgba(0, 0, 0, 0.1);
  border-radius: 5px;
  padding: 15px 20px;
}

.circle-label-info .label-item {
  display: flex;
  align-items: center;
  margin-bottom: 10px;
}

.circle-label-info .label-item:last-child {
  margin-bottom: 0;
}

.circle-label-info .label-item .label-title {
  font-size: 12px;
  color: var(--fontgray2);
  position: relative;
  min-width: 60px;
  text-align: left;
}

.circle-label-info .label-item .label-desc {
  font-size: 14px;
  color: #000000;
  font-weight: 500;
}

.circle-label-info .label-item .label-title:after {
  background-color: var(--primary);
}

.circle-label-info .label-item.sky .label-title:after {
  background-color: var(--skyBlue);
}

.circle-label-info .label-item.navy .label-title:after {
  background-color: var(--navy);
}

.circle-label-info .label-item .label-title::after {
  content: "";
  position: absolute;
  left: -20px;
  width: 10px;
  height: 10px;
  border-radius: 50%;
}

.search-input-con {
  width: 100%;
  position: relative;
}

.search-input-con .input-right-x {
  position: absolute;
  display: flex;
  align-items: center;
  height: 100%;
  right: 15px;
  top: 0;
}

.search-input-con .input-right-x button {
  width: 16px;
  height: 16px;
}

.search-input-con input {
  width: 100%;
  border: 1px solid var(--border-gray50);
  border-radius: 22px;
  padding: 0px 44px;
  background-image: url("../img/search.png");
  background-size: 16px;
  background-repeat: no-repeat;
  background-position: left 16px center;
  font-size: 14px;
  color: #c1c0c8;
  box-sizing: border-box;
  height: 40px;
}

.scroll .tag-item {
  flex-shrink: 0;
  margin-bottom: 0;
}

.bottom-hash {
  display: flex;
}

.default-icon.bck {
  width: 32px;
  height: 32px;
  background-repeat: no-repeat;
  background-position: center;
  background-size: 32px;
  display: inline-block;
}

.default-icon.bck.aa {
  background-image: url("../img/aa.png");
}

.default-icon.bck.clock {
  background-image: url("../img/clock.png");
}

.default-icon.bck.book {
  background-image: url("../img/book.png");
}

.default-icon.bck.glass {
  background-image: url("../img/glass.png");
}

.default-icon.bck.goal {
  background-image: url("../img/goal.png");
}

.default-icon img {
  width: 30px;
  height: 30px;
}

.info-box .default-icon {
  width: 32px;
  height: 32px;
}

.default-icon.middle img {
  width: 24px;
  height: 24px;
}

.small-icon img {
  width: 15px;
  height: 15px;
}

.chart-con {
  background: #ffffff;
  box-shadow: 3px 8px 22px 0 rgba(0, 0, 0, 0.1);
  border-radius: 5px;
}

.lock {
  position: relative;
}

.lock.op::after {
  background-color: rgba(255, 255, 255, 0.5);
}

.lock:after {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: rgba(255, 255, 255, 0.1);
  left: 0;
  top: 0;
  backdrop-filter: blur(4px);
  -webkit-backdrop-filter: blur(4px);
  background-image: url("../img/lock.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: 54px;
}

.dot-wrap {
  position: relative;
}

.dot-con img {
  width: 24px;
  height: 24px;
}

.dot-wrap .dot-desc-con {
  box-shadow: 3px 8px 22px 0 rgba(0, 0, 0, 0.05);
  border: 1px solid var(--lightWite);
  position: absolute;
  width: 150px;
  border-radius: 5px;
  left: -135px;
  top: 20px;
  transform: scale(0);
  opacity: 0;
  transition: 0.3s;
  transform-origin: right top;
  background-color: white;
  z-index: 10;
}

.dot-wrap .dot-desc-con.active {
  transform: scale(1);
  opacity: 1;
  transition: 0.3s;
  transform-origin: right top;
}

.dot-wrap .dot-desc-con .dot-font {
  font-size: 14px;
  font-weight: 300;
  color: var(--color-gray);
  padding: 15px;
  letter-spacing: -0.05rem;
}

.dot-wrap .dot-desc-con .dot-font:hover,
.dot-wrap .dot-desc-con .dot-font.active {
  background-color: var(--lightWite50);
}

.dim {
  position: absolute;
  width: 100%;
  height: 100%;
  opacity: 0;
  z-index: 100;
  top: 0;
  left: 0;
}

.bottom-fixed {
  width: 100%;
  padding-left: 20px;
  padding-right: 20px;
  box-sizing: border-box;
  position: fixed;
  bottom: 20px;
}

.img-inner-info {
  position: absolute;
  right: 20px;
  top: 10px;
}

.desc-info-box {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: var(--primary);
  box-shadow: 3px 5px 22px 0 rgba(0, 0, 0, 0.1);
  padding: 16px 20px;
  border-radius: 5px;
}

.custom-calendar-wrap .react-calendar.custom-calendar {
  width: 100%;
  border: none;
}

.custom-calendar-wrap .react-calendar__month-view__weekdays {
  margin-bottom: 33px;
}

.custom-calendar-wrap .react-calendar__month-view__weekdays__weekday abbr {
  color: var(--gray);
  font-size: 12px;
  text-decoration: none;
  font-weight: 100;
  margin-bottom: 33px;
}

.custom-calendar-wrap .react-calendar__tile--hasActive {
  background-color: white;
}

.custom-calendar-wrap .react-calendar__tile {
  font-size: 12px;
  padding-top: 0;
  padding-bottom: 0;
  margin-bottom: 35px;
  font-weight: 400;
  color: var(--gray);
  line-height: 18px;
}

.custom-calendar-wrap .react-calendar__month-view__days__day--neighboringMonth {
  color: var(--gray30);
}

.custom-calendar-wrap .react-calendar__tile.react-calendar__tile--active {
  background-color: var(--primary);
  color: white !important;
  border: none !important;
}

.custom-calendar-wrap .react-calendar__tile.react-calendar__tile--rangeStart {
  border-top-left-radius: 20px;
  border-bottom-left-radius: 20px;
}

.custom-calendar-wrap .react-calendar__tile.react-calendar__tile--rangeEnd {
  border-top-right-radius: 20px;
  border-bottom-right-radius: 20px;
  background-color: var(--primary);
  color: white !important;
}

.level-info-con {
  background: #ffffff;
  box-shadow: 3px 8px 22px 0 rgba(0, 0, 0, 0.1);
  border-radius: 5px;
  padding: 40px 20px;
}

.sqare-tab-con {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
}

.tag-wrap {
  overflow: hidden;
}

.sqare-tab-con .sqare-tab {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 33.33333%;
  border: 1px solid var(--lightWite);
  box-sizing: border-box;
  margin-top: -1px;
  margin-left: -1px;
  height: 35px;
}

.sqare-tab-con .sqare-tab p {
  font-size: 12px;
}

.sqare-tab-con .sqare-tab.active {
  background-color: var(--lightWite);
}

.sqare-tab-con .sqare-tab.active p {
  font-weight: 500;
}

.accodion-row {
  margin-bottom: 13px;
}

.accodion-question {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-bottom: 13px;
  border-bottom: 1px solid var(--lightWite);
  margin-right: 20px;
  margin-left: 20px;
}

.accodion-anser {
  background-color: var(--lightWite);
  padding: 20px;
  transform: scale(0, 0);
  opacity: 0;
  transform-origin: top;
  display: none;
}

.accodion-row.active .accodion-question {
  border-bottom: none;
}
.accodion-row.active .accodion-anser {
  transform: scale(1, 1);
  opacity: 1;
  transform-origin: top;
  display: block;
}

.shadow-box {
  background: #ffffff;
  box-shadow: 3px 5px 22px rgba(0, 0, 0, 0.1);
  border-radius: 5px;
}

.member-row {
  padding: 15px 0px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-bottom: 1px solid var(--lightWite);
}

.step-card {
  background: #ffffff;
  box-shadow: 3px 8px 22px 0 rgba(0, 0, 0, 0.1);
  border-radius: 5px;
  padding: 20px 20px 45px 20px;
  height: 227px;
  position: relative;
  box-sizing: border-box;
}

.step-card.width-icon {
  padding: 20px 10px 45px 20px;
}

.step-card .scroll-wrap {
  display: block;
  overflow-y: auto;
  height: 100%;
}

.step-card .scroll-wrap .left-content {
  width: calc(100% - 60px);
}

.sound-icon-con {
  width: 40px;
  height: 40px;
  position: relative;
}

.sound-icon.absol {
  position: absolute;
  z-index: 2;
  margin: 10px;
  right: 0px;
  top: 0px;
}

.sound-icon.absol.type2 {
  right: -20px;
}

.emigi-con {
  min-height: 190px;
  box-sizing: border-box;
  background: #ffffff;
  box-shadow: 3px 8px 22px 0 rgba(0, 0, 0, 0.05);
  border-radius: 5px;
  width: 100%;
  padding-top: 20px;
  padding-bottom: 20px;
  text-align: center;
}

.emigi-wrap {
  display: flex;
  background-position: center;
  background-repeat: no-repeat;
  background-image: url("../img/right-blue-arr.png");
  background-size: 16px;
}

.emigi-con:nth-child(odd) {
  margin-right: 15px;
}

.emigi-con:nth-child(even) {
  margin-left: 15px;
  border: 1px solid var(--primary);
}

.one-line-elis {
  display: inline-block;
  max-width: 300px;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}

.content-scroll-container {
  height: calc(100vh - 230px);
  overflow-y: auto;
}

.content-scroll-container.type2 {
  height: calc(100vh - 90px);
  overflow-y: auto;
}

.word-content-container {
  width: calc(100% - 60px);
}
